body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background: rgb(212,230,241);
background: -webkit-linear-gradient(45deg,  rgba(212,230,241,1) 0%,rgba(215,189,226,1) 100%);
background: linear-gradient(45deg,  rgba(212,230,241,1) 0%,rgba(215,189,226,1) 100%);
filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#d4e6f1', endColorstr='#d7bde2',GradientType=1 );

background-image: url(/my-stock-monitor/static/media/1.a5bf0101.jpg);
background-size: cover;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.triangle{
    font-size: 2em;
    line-height: 12px;
    
}
.card {
    margin: .25em;
    border: .2em solid rgb(66, 66, 66);
    border-radius: 10px;
    width: 7em;
    height: 7em;
    font-size: 1.5vh;
    line-height: 1vh;
    text-align: center;
    -webkit-transition-duration: .2s;
            transition-duration: .2s;
    background-color: rgb(224, 242, 241);
}

.card div {
    color: rgb(66, 66, 66);
    margin: .7em 0em;
}

a {
    text-decoration: none;
    color: black;
}

.card:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    box-shadow: 3px 3px 5px rgb(117, 117, 117);
    background-color: white;
}

.title {
    font-size: 1.4em;
}

.price {
    font-size: 1.75em;
    font-weight: bolder;
}

.rate {
    vertical-align: text-top;
}
.cardList {
    /* border: 2px solid red; */
    margin: 0 0 5vh;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-content: center;
            align-content: center;
}
.App {
    margin-top: 1vw;
    margin-bottom: auto;
    color: rgb( 234, 242, 248 ); 
}

p {
    font-size: 4vh;
    text-align: center;
    margin: 0vh;
    padding: 0vh;   
}
